export default {
    data(){
        return{
			user: this.$store.state.userInfo,
			Patient: this.$store.state.patient,
			SubjectBuyRecordId:0,
			info:'',
			showConfirm:false,
        }
    },
    created(){
		this.$bridge.callhandler('printdata',"121212")
		this.$bridge.callhandler('getSubjectBuyRecordId','',(data) => {
            this.SubjectBuyRecordId = data
			this.getInfo(data)
		    responseCallback('接收到方法')
        })
	},
    mounted(){
		//this.getInfo(889) //634
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		goBack(){
			this.showConfirm = true
		},
		confirm(){
			const data = '123'
            this.$bridge.callhandler('Back', data)
		},
		getInfo(data){
			this.$get('MDTAPI/MDT/QueryMDTBySubjectBuyRecordId',{
				SubjectBuyRecordId:data
			}).then(o => {
				this.info = o
			})
		},
		ClickRecord(){
			this.$post('MDTAPI/MDT/SaveDiseaseInfo',{
				SubjectBuyRecordId:this.SubjectBuyRecordId,
				PatientId:this.info.PatientId,
				PatientName:this.info.PatientName,
				Description:this.info.Description,  //病情描述
				Diseases:this.info.Diseases,	//疾病和症状
				DiseasesTimeNo:this.info.DiseasesTimeNo,	//患病时长序号
				DiseasesTimeText:this.info.DiseasesTimeText,	//患病时长描述
				SeeDoctorNo	:this.info.SeeDoctorNo,      //就诊序号
				SeeDoctorText:this.info.SeeDoctorText,	//就诊描述
				SeeDoctor:this.info.SeeDoctor,	//就诊医院
				SeeDepartment:this.info.SeeDepartment,	//就诊科室
				IsHasImg:this.info.IsHasImg,	//是否有图片(0-否，1-是)-图片存储在就诊病历
				IsDrug :this.info.IsDrug,		//是否用药(0-否，1-是)
				DrugUse:this.info.DrugUse,	   //使用药品
				OperationDes:this.info.OperationDes,	//手术描述
				ChemoDes:this.info.ChemoDes,	//放化疗描述
				ChronicDisease:this.info.ChronicDisease,	//慢性疾病
				SeriousDisease:this.info.SeriousDisease,	//重大疾病
				IsAllergy:this.info.IsAllergy,	//是否过敏(0-否，1-是)
				AllergyDes:this.info.AllergyDes,	//过敏描述
				MedicalPathList:null,	//病历图片
				DrId:this.user.Data.User.DoctorId, //修改或者添加病情医生Id
				DrName:this.user.Data.User.DoctorName, //修改或者添加病情医生名称*/
			}).then(o => {
				if(o){
                    const data = '123'
                    this.$bridge.callhandler('Back', data)
                }
			})
		}
	
    }
}



